<template>
	<div class="wrap_login">
		<div class="login_div">
			<!-- 用于防止表单填充 -->
			<h3 class="title_logo">
				<img src="../../assets/images/swissmic-logo-black.png" alt="" >
				<span class="title_span">MS</span>	
			</h3>
			<span class="zh_en">
				<el-dropdown>
				  	<span class="el-dropdown-link">
				   		<img src="../../assets/images/zhen.png" alt="">
				  	</span>
				  	<el-dropdown-menu slot="dropdown" hide-on-click="true">
				    	<el-dropdown-item><span class="span_lang" @click="changeChina">{{$t("common.zh")}}</span></el-dropdown-item>
				    	<el-dropdown-item><span class="span_lang" @click="changeEnglish">{{$t("common.en")}}</span></el-dropdown-item>
				    </el-dropdown-menu>
				</el-dropdown>				
			</span>
				<div>
			    	<div class="login_input">
			    		<input v-model="userForm.username"  :placeholder="$t('user.account')" class="user_input el-input__inner"></input>
			    	</div>
			    	<div class="login_input">
			    		<input type="password" v-model="userForm.password"  :placeholder="$t('user.password')" class="password_input el-input__inner" @keyup.enter="login"></input>
			    	</div>
			  	<button class="login_btn el-button" @click.prevent="login" >{{$t("common.login")}}</button>
			  	<div class="login_tips" v-if="normalErr">{{$t('login.PasswordOrUserIsInvalid')}}</div>
			  	<div class="login_tips" v-if="otherErr">{{errTips}}</div>
			  </div>
		</div>
	</div>
</template>
<script>
import { mapMutations } from 'vuex'
import { Debounce } from '@/utils/public'
// import { loginApi } from '../../request/api/login';// 导入我们的api接口
import QS from 'qs'; 
export default{
	data()	{
		return{
			userForm:{
				username:'',
				password:''
			},
			accounttips:'',
			passwordtips:'',
			normalErr:false,
			otherErr:false,
			errTips:'',
		}
	},
	mounted() {

	},
	methods:{
		...mapMutations(['changeLogin']),
		changeChina(){
			this.$i18n.locale = 'zh';
			localStorage.setItem('lang', 'zh')
		},
		changeEnglish(){
			this.$i18n.locale = 'en';
			localStorage.setItem('lang', 'en')
		},
		login: Debounce(function(){
			let _this = this
			if (this.userForm.username && this.userForm.password) {
				this.normalErr = false
				this.$http({
					url: '/login',
					method: 'post',
					data: {
				  	username: _this.userForm.username,
				  	password: _this.userForm.password
					},
					headers:{'Content-Type':'application/json'}
				}).then(res=>{
					if (res.status ==200) {
						if (res.data.result) {
							let data = res.data.data;
							console.log(data)
							localStorage.setItem('user_name',data.user_name)
							localStorage.setItem('user_group_power',data.user_group_power)
							// _this.changeLogin({ user: data.user_data.user_name});		
							_this.$router.push('/report')		
						}else{
							_this.otherErr = true;
							_this.errTips = res.data.msg;
							setTimeout(function(){
								_this.otherErr = false;
							},3000)
						}
					}else{

					}							
				})
					sessionStorage.setItem('btnPermissions',2)
				} else {
					this.normalErr = true
				}
			},2000)
		}
} 
</script>
<style scoped>
	.wrap_login{
		position: fixed;
		width:100%;
		height: 100%;
	}
	.zh_en{
		position: absolute;
		top: 16px;
		right: 0;
		cursor: pointer;
	}
	.zh_en img{
		width:28px;
		height: 28px;
	}
	.login_div{
		width:420px;
		height: 260px;
		position: relative;
		top: 50%;
		left: 50%;
		margin:-130px 0 0 -210px;
	}
	.login_div input{
		border: 2px solid #dadfe6;
		border-radius: 2px;
	}
	.login_input{
		margin:20px 0;
		font-size: 14px;
	}
	.entertips{
		display: block;
		text-align: left;
		color: red;
		height: 20px;
		line-height: 20px;
		padding-left: 2px;
	}
	.user_input:focus {
	    outline: none;
	    border: 2px solid #dadfe6;
	}
	.password_input:focus{
		 outline: none;
	    border: 2px solid #dadfe6;
	}
	.title_logo{
		margin-bottom: 30px;
	}
	.title_logo img{
		width:136px;
		height: 40px;
	}
	.title_span{
		height: 40px;
		line-height: 40px;
		font-size: 24px;
		color: #181818;
		padding-left: 10px;
		font-weight: 400;
	}
	.span_lang {
		display: inline-block;
		color: #666666!important;
		height: 30px;
		width:60px;
		line-height: 30px;
	}
	.span_lang:hover{
	color: #74d6e7!important;
	}
	.login_btn{
		width:420px;
		height: 40px;
		background: #74d6e7;
		color: #ffffff;
		font-size: 16px;
	}
	.login_tips{
		height: 30px;
		line-height: 30px;
		color: red;
		background: #f1f5f8;
		margin-top: 10px;
		border-radius: 2px;
	}
	.login_div .el-button:active{
		border-color: #5dcfe3;
		background: #5dcfe3;
		box-shadow: 0 0 0 0 #50b2c3, 0 0 20px 0 #5dcfe3;
	}
</style>
